/**
 * Flattens simple data
 * Returns an array of tuples (x value, simple point)
 *
 * @param simpleData
 * @param series
 * @param inDataSpace
 * @return {[]}
 */
export default function flattenSimpleData(simpleData, {series, inDataSpace }) {
    const flattened = [];
    let fI = 0;

    for (let point of simpleData) {
        if (Array.isArray(point[series.yKey])) {
            for (let subpoint of point[series.yKey]) {
                if (series.ignoreDiscontinuities && typeof subpoint[series.yKey] !== 'number') {
                    continue;
                }

                flattened.push([inDataSpace[fI++][0], subpoint]);
            }

            continue;
        }

        if (series.ignoreDiscontinuities && typeof point[series.yKey] !== 'number') {
            continue;
        }

        flattened.push([inDataSpace[fI++][0], point]);
    }

    if (flattened.length !== inDataSpace.length)  {
        console.warn('Flattening didn\'t give the same length as it has in data space'); // eslint-disable-line no-console
    }


    return flattened;
}
