import pyodideReady from '../../../helpers/pyodide_ready.js';

const pyCacheArgs = {};
const pyCacheResults = {};
const pyCachePromises = {};

export function getPythonFunctionWrapper(name) {
    return async (args, { currentIndices }) => {
        if (Object.values(currentIndices).length !== 1) {
            throw new Error('Python functions currently only accept one series passed in');
        }

        const currentIndex = Object.values(currentIndices)[0];

        if (pyCachePromises[name]) {
            await pyCachePromises[name];
        }

        let lastRunArgs = pyCacheArgs[name] || [];
        let lastResult = pyCacheResults[name];

        let argsMatch = args.length === lastRunArgs.length;

        if (argsMatch) {
            for (let i = 0; i < args.length; i++) {
                if (args[i] !== lastRunArgs[i]) {
                    argsMatch = false;
                    break;
                }
            }
        }

        if (argsMatch && lastResult !== undefined) {
            return lastResult[currentIndex];
        }

        const resultPromise = runPythonFunc(args, name).then((result) => {
            pyCacheArgs[name] = args;
            pyCacheResults[name] = result;
            return result;
        });
        pyCachePromises[name] = resultPromise; // eslint-disable-line require-atomic-updates

        const result = await resultPromise;

        return result[currentIndex];
    };
}

const loadedPackages = {};

/**
 * Runs the python function at a low level
 * No munging of outputs or memoization
 *
 * @param {Array<[[Number, Number]]|Number>} args - the arguments to pass in to the python function.
 * @param {String} pyfunc - the python function name to run, eg scipy.ndimage.gaussian_filter
 * @return {Promise<Float64Array>}
 */
export async function runPythonFunc(args, pyfunc) {
    await pyodideReady();

    const pyfuncPieces = pyfunc.split('.');
    const pyfuncName = pyfuncPieces[pyfuncPieces.length - 1];
    const pyfuncImport = pyfuncPieces.slice(0, -1).join('.');

    const pyPackages = ['numpy', pyfuncPieces[0]];
    await Promise.all(pyPackages.map((pyPackage) => {
        if (loadedPackages[pyPackage]) {
            return Promise.resolve();
        }

        return window.pyodide.loadPackage(pyPackage).then(() => {
            loadedPackages[pyPackage] = true;
        });
    }));

    const globalKeys = [];
    let pyArgs = [];
    for (let arg of args) {
        const globalKey = `pyodideArg${Math.random().toString(16).slice(2)}`;

        if (Array.isArray(arg)) {
            window[globalKey] = new Float64Array(arg.map((tuple) => tuple[1]));
            pyArgs.push(`np.asarray(js.${globalKey})`);
        } else {
            window[globalKey] = arg;
            pyArgs.push(arg);
        }

        globalKeys.push(globalKey);
    }

    const script = `
        import js
        import numpy as np
        from ${pyfuncImport} import ${pyfuncName}
        ${pyfuncName}(${pyArgs.join(', ')})
    `;

    const result = window.pyodide.runPython(script);

    for (let globalKey of globalKeys) {
        delete window[globalKey];
    }

    return result;
}
