import {lexTokens} from './lex_tokens';
import parseTokens from './parse_tokens';

/**
 * Parses an abstract syntax tree, plus metadata
 *
 * @param {String} formula
 * @return {{root: Object, flags: [String], formula: String, dependencies: Set<String>}}
 */
export default function parseAST(formula) {
    let strippedFormula = formula;

    let flags = [];
    const flagsStartIndex = formula.lastIndexOf(',');
    if (flagsStartIndex > formula.lastIndexOf(')')) {
        flags = formula.slice(flagsStartIndex + 1)
            .split(' ')
            .map((flag) => flag.trim())
            .filter((flag) => flag);

        strippedFormula = formula.slice(0, flagsStartIndex);
    }

    const tokens = lexTokens(parseTokens(strippedFormula));
    const dependencies = new Set();

    return {
        root: parseExpression({
            tokens,
            startIndex: 0,
            endIndex: tokens.length
        }, {
            dependencies
        }),
        flags: new Set(flags),
        dependencies,
        formula
    };
}

function parseExpression({tokens, startIndex, endIndex}, { depth=0, dependencies }={}) {
    // sanity checks
    if (depth > 1000) {
        throw new Error('Max depth exceeded');
    }

    if (startIndex >= endIndex) {
        throw new Error('Invalid bounds');
    }

    // base case
    if (startIndex + 1 === endIndex) {
        const { type, token } = tokens[startIndex];

        if (type === 'constant') {
            return {
                type: 'constant',
                value: parseFloat(token)
            };
        }

        if (type === 'identifier') {
            dependencies.add(token);

            return {
                type: 'identifier',
                name: token
            };
        }

        throw new Error('Invalid base case');
    }

    // expression with parens
    if (tokens[startIndex].token === '(' && tokens[endIndex - 1].token === ')') {
        return parseExpression({
            tokens,
            startIndex: startIndex + 1,
            endIndex: endIndex - 1
        }, { depth: depth + 1, dependencies });
    }

    const operatorExpression = parseOperators({ tokens, startIndex, endIndex}, { depth, dependencies });
    if (operatorExpression) {
        return operatorExpression;
    }

    if (tokens[startIndex].type === 'function') {
        return parseFunction({ tokens, startIndex, endIndex}, { depth, dependencies });
    }

    if (tokens[startIndex].type === 'identifier' && tokens[startIndex + 1].type === 'modifier') {
        return parseModifier({ tokens, startIndex, endIndex }, { dependencies });
    }

    throw new Error('Invalid formula');
}

function parseModifier({ tokens, startIndex, endIndex}, { dependencies }) {
    const argumentsList = [];

    if (endIndex - startIndex >= 4) {
        if (tokens[startIndex + 2].token !== '(' || tokens[endIndex - 1].token !== ')') {
            throw new Error('Modifier has bad parens');
        }

        // get past identifier, modifier, and open paren
        let currentArgumentStart = startIndex + 3;
        let expectingComma = false;

        for (let i = currentArgumentStart; i < endIndex - 1; i++) {
            const { token, type } = tokens[i];
            if (type === 'constant') {
                argumentsList.push(parseFloat(token));
                expectingComma = true;
            } else if (type === 'identifier') {
                argumentsList.push(token);
                expectingComma = true;
            } else if (type === 'comma') {
                if (!expectingComma) {
                    throw new Error('Unexpected comma in modifier');
                }

                expectingComma = false;
            } else if (i < endIndex - 2 && type === 'unary-operator' && token === '-') {
                // note: this is hackier than evaluating the ast recursively like we do for functions
                // but, modifiers are meant to have constant arguments, so this simplifies things
                if (tokens[i + 1].type !== 'constant') {
                    throw new Error('Expecting constant after unary operator in modifier');
                }

                argumentsList.push(-parseFloat(tokens[i + 1].token));
                i++;
            } else {
                throw new Error(`Unexpected ${type} in modifier`);
            }
        }
    } else if (endIndex - startIndex === 3) {
        throw new Error('Modifier has bad parens');
    }

    const name = tokens[startIndex].token;
    dependencies.add(name);

    return {
        type: 'identifier',
        name,
        modifier: {
            selector: tokens[startIndex + 1].token,
            arguments: argumentsList
        }
    };
}

function parseFunction({ tokens, startIndex, endIndex}, { depth, dependencies }) {
    let parenDepth = 0;

    const argumentsList = [];
    let currentArgumentStart = startIndex + 2; // add two to get past name and open paren

    if (tokens[startIndex + 1].token !== '(' || tokens[endIndex - 1].token !== ')') {
        throw new Error('Function has bad parens');
    }

    // subtract one to get past close paren
    for (let i = currentArgumentStart; i < endIndex - 1; i++) {
        const {token, type} = tokens[i];

        if (type === 'paren') {
            if (token === '(') {
                parenDepth++;
            } else {
                parenDepth--;
            }
        }

        if (parenDepth > 0) {
            continue;
        }

        if (type === 'comma') {
            argumentsList.push(parseExpression({
                tokens,
                startIndex: currentArgumentStart,
                endIndex: i
            }, { depth: depth + 1, dependencies }));
            currentArgumentStart = i + 1;
        }
    }

    if (parenDepth > 0) {
        throw new Error('Mismatched parens');
    }

    if (currentArgumentStart !== endIndex - 1) {
        argumentsList.push(parseExpression({
            tokens,
            startIndex: currentArgumentStart,
            endIndex: endIndex - 1
        }, { depth: depth + 1, dependencies }));
    }

    return {
        type: 'function',
        name: tokens[startIndex].token,
        arguments: argumentsList
    };
}

function parseOperators({ tokens, startIndex, endIndex}, { depth, dependencies }) {
    let parenDepth = 0;
    let lowestPrecedence = Infinity;
    let lowestPrecedenceIndex = null;

    // find the operator with lowest precedence, ignoring stuff within parentheses
    for (let i = startIndex; i < endIndex; i++) {
        const { token, type } = tokens[i];

        if (type === 'paren') {
            if (token === '(') {
                parenDepth++;
            } else {
                parenDepth--;
            }
        }

        if (parenDepth > 0) {
            continue;
        }

        const precedence = precedenceOf({ token, type });

        if (isFinite(precedence) && precedence <= lowestPrecedence) {
            lowestPrecedenceIndex = i;
            lowestPrecedence = precedence;
        }
    }

    if (parenDepth > 0) {
        throw new Error('Mismatched parens');
    }

    if (lowestPrecedenceIndex === null) {
        return false;
    }

    const rightExpression = parseExpression({
        tokens,
        startIndex: lowestPrecedenceIndex + 1,
        endIndex
    }, { depth: depth + 1, dependencies });

    if (startIndex === lowestPrecedenceIndex) {
        return {
            type: 'function',
            name: tokens[lowestPrecedenceIndex].token,
            arguments: [
                rightExpression
            ]
        };
    }

    const leftExpression = parseExpression({
        tokens,
        startIndex,
        endIndex: lowestPrecedenceIndex
    }, { depth: depth + 1, dependencies });

    return {
        type: 'function',
        name: tokens[lowestPrecedenceIndex].token,
        arguments: [
            leftExpression,
            rightExpression
        ]
    };
}

function precedenceOf({ token, type }) {
    if (type === 'unary-operator') {
        return 3;
    }

    if (type === 'infix-operator') {
        if (token === '*' || token === '/' || token === '%') {
            return 2;
        } else {
            return 1;
        }
    }

    return Infinity;
}
