import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import StateController from '../state/state_controller';
import {useUserCreatedSeries} from '../state/hooks';
import nameForSeries from '../helpers/name_for_series.js';

export default React.memo(FormulasEditor);

function FormulaBox({ stateController, onEnter, formulaValue, setFormulaValue }) {
    const id = useState(`formula-${Math.random().toString(36).slice(2)}`)[0];

    useEffect(() => {
        const listener = (singleSeries, index) => {
            // Note: this does not use nice react stuff because we have to use the cursor
            // So, it's ugly and breaks the mold, but we'll deal with it
            // TODO: replace with a useRef at least

            if (id !== stateController.blurredFormulaID) {
                return;
            }

            const name = nameForSeries(singleSeries, index);

            const el = document.getElementById(id);
            const caretPosition = el.selectionStart;
            const value = el.value.slice(0, caretPosition) + `"${name}"` + el.value.slice(caretPosition);
            el.value = value;
            setFormulaValue(value);

            el.focus();
            el.setSelectionRange(caretPosition + name.length + 2, caretPosition + name.length + 2);
        };

        stateController.on('series_click', listener);

        return () => {
            stateController.off('series_click', listener);
        };
    }, []);

    return (
        <textarea
            placeholder="a + b"
            className="formula-formula"
            id={id}
            value={formulaValue}
            onChange={(e) => setFormulaValue(e.target.value)}
            onKeyUp={(e) => e.keyCode === 13 && onEnter()}
            rows={1}
            onBlur={() => stateController.blurredFormulaID = id}
        />
    );
}

FormulaBox.propTypes = {
    stateController: PropTypes.instanceOf(StateController).isRequired,
    onEnter: PropTypes.func.isRequired,
    formulaValue: PropTypes.string.isRequired,
    setFormulaValue: PropTypes.func.isRequired
};

function EditedFormula({ singleSeries, stateController }) {
    const name = singleSeries.name;
    const formula = singleSeries.data.formula;

    const [newName, setNewName] = useState(name);
    const [newFormula, setNewFormula] = useState(formula);

    const canSave = newFormula !== formula;

    const save = () => {
        if (!canSave) {
            return;
        }

        stateController.updateUserCreatedSeries(singleSeries, { name: newName, formula: newFormula });
    };

    const remove = () => {
        stateController.deleteUserCreatedSeries(singleSeries);
    };

    const hasError = singleSeries.formulaParseError || singleSeries.formulaEvaluationError;

    return (
        <div className={`formula-row${hasError ? ' formula-row-has-error' : ''}`}>
            <input
                placeholder="Name"
                className="formula-name"
                value={newName}
                onChange={(e) => setNewName(e.target.value)}
                onKeyUp={(e) => e.keyCode === 13 && save()}
            />

            <FormulaBox
                stateController={stateController}
                onEnter={save}
                formulaValue={newFormula}
                setFormulaValue={setNewFormula}
            />

            {
                canSave ?
                    <button onClick={save}>
                        Save
                    </button> :
                    <span className="save-button-placeholder" />
            }

            <button onClick={remove}>
                Delete
            </button>

            {
                singleSeries.formulaParseError &&
                <div className="formula-error-message">
                    {singleSeries.formulaParseError.message}
                </div>
            }

            {
                singleSeries.formulaEvaluationError &&
                !singleSeries.formulaParseError &&
                <div className="formula-error-message">
                    {singleSeries.formulaEvaluationError.message}
                </div>
            }
        </div>
    );
}

EditedFormula.propTypes = {
    stateController: PropTypes.instanceOf(StateController).isRequired,
    singleSeries: PropTypes.object.isRequired
};

function FormulasEditor({ stateController }) {
    const userCreatedSeries = useUserCreatedSeries(stateController);

    const [newName, setNewName] = useState('');
    const [newFormula, setNewFormula] = useState('');

    const addNew = () => {
        stateController.addUserCreatedSeries({
            name: newName,
            data: {
                formula: newFormula
            }
        });
        setNewName('');
        setNewFormula('');
    };

    return (
        <div className="formulas-editor">
            <div className="formulas-inner">

                {
                    userCreatedSeries.map((singleSeries, i) => <EditedFormula
                        key={i}
                        singleSeries={singleSeries}
                        stateController={stateController}
                    />)
                }

                <div className="formula-row">
                    <input
                        placeholder="Name"
                        className="formula-name"
                        value={newName}
                        onChange={(e) => setNewName(e.target.value)}
                        onKeyUp={(e) => e.keyCode === 13 && addNew()}
                    />

                    <FormulaBox
                        stateController={stateController}
                        onEnter={addNew}
                        formulaValue={newFormula}
                        setFormulaValue={setNewFormula}
                    />

                    <button onClick={addNew}>
                        Add
                    </button>
                </div>
            </div>
        </div>
    );
}

FormulasEditor.propTypes = {
    stateController: PropTypes.instanceOf(StateController).isRequired
};
