import parseTokens from './parse_tokens';

/**
 * Lexes tokens, adding metadata to them
 *
 * @param {[String]|String} tokens  - the tokens to lex. If passed a string, will call parseTokens first
 * @return {[{ type: String, token: String }]}
 */
export function lexTokens(tokens) {
    if (!Array.isArray(tokens)) {
        tokens = parseTokens(tokens);
    }

    const lexed = [];

    let isModifier = false;

    for (let i = 0; i < tokens.length; i++) {
        const token = tokens[i];

        const isOperator = (token === '+') ||
            (token === '-') ||
            (token === '/') ||
            (token === '*') ||
            (token === '%');

        if (isOperator) {
            if (lexed.length === 0) {
                lexed.push({
                    type: 'unary-operator',
                    token
                });
                continue;
            }

            const prevType = lexed[lexed.length - 1].type;

            if (prevType === 'comma' || prevType === 'infix-operator' || prevType === 'paren') {
                lexed.push({
                    type: 'unary-operator',
                    token
                });
                continue;
            } else {
                lexed.push({
                    type: 'infix-operator',
                    token
                });
                continue;
            }
        }

        if (token === '(' || token === ')') {
            lexed.push({
                type: 'paren',
                token
            });
            continue;
        }

        if (token === ',') {
            lexed.push({
                type: 'comma',
                token
            });
            continue;
        }

        if (token === ':') {
            isModifier = true;
            continue;
        }

        if (isModifier) {
            lexed.push({
                type: 'modifier',
                token
            });
            isModifier = false;
            continue;
        }

        if (/^\d+(\.\d+)?$/.test(token)) {
            lexed.push({
                type: 'constant',
                token
            });
            continue;
        }

        if (i < tokens.length - 1 && tokens[i + 1] === '(') {
            lexed.push({
                type: 'function',
                token
            });
            continue;
        }

        lexed.push({
            type: 'identifier',
            token
        });
    }

    return lexed;
}
