export const OPERATOR_FUNCTIONS = {
    '+': ([a, b]) => {
        if (b === undefined) {
            return Math.abs(a);
        }

        return a + b;
    },
    '-': ([a, b]) => {
        if (b === undefined) {
            return -a;
        }

        return a - b;
    },
    '*': ([a, b]) => a * b,
    '/': ([a, b]) => a / b,
    '%': ([a, b]) => a % b
};
