import lookUpFunction from './look_up_function';

export default function evaluateFormula(singleSeries, { series }) {
    if (!singleSeries.ast) { // ast parsing must have failed; do nothing
        return [];
    }

    if (singleSeries.data.formula !== singleSeries.ast.formula) {
        throw new Error('AST refers to different formula');
    }

    const { dependencies, root, flags } = singleSeries.ast;

    if (!dependencies.size) {
        return [];
    }

    const dataByName = extractDataByName(series);
    const { minX, maxX } = analyzeDependencyBounds({ dependencies, dataByName });

    const currentIndices = {};
    let currentX = minX;

    let result = [];
    let isAsync = false;

    while (currentX <= maxX) {
        // set currentIndices such that for each dependency, data[index] <= currentX
        for (let dependency of dependencies) {
            const data = dataByName[dependency];
            let currentIndex = currentIndices[dependency];

            while (
                (currentIndex === undefined || currentIndex < data.length - 1) &&
                (data[currentIndex === undefined ? 0 : currentIndex + 1][0] <= currentX)
                )
            {
                if (currentIndex === undefined) {
                    currentIndex = 0;
                } else {
                    currentIndex++;
                }
            }

            currentIndices[dependency] = currentIndex;
        }

        const y = evaluateAST(root, { dataByName, currentIndices, currentX });

        result.push([
            currentX,
            y
        ]);

        isAsync = isAsync || y instanceof Promise;

        let nextX = Infinity;
        for (let dependency of dependencies) {
            const data = dataByName[dependency];
            const nextIndex = typeof currentIndices[dependency] === 'number' ? currentIndices[dependency] + 1 : 0;

            if (nextIndex >= data.length) {
                continue;
            }

            if (data[nextIndex][0] < nextX) {
                nextX = data[nextIndex][0];
            }
        }

        currentX = nextX;
    }

    if (isAsync) {
        return Promise.all((result).map((tuple) => tuple[1]))
            .then((yValues) => {
                const resolvedResult = [];
                for (let i = 0; i < result.length; i++) {
                    resolvedResult.push([result[i][0], yValues[i]]);
                }

                return finalizeResult(resolvedResult, flags);
            });
    }

    return finalizeResult(result, flags);
}

function finalizeResult(result, flags) {
    if (!flags.has('nullable')) {
        result = result.filter(([_x, y]) => y !== null);
    }

    return result;
}

function extractDataByName(series) {
    const dataByName = {};
    for (let singleSeries of series) {
        const name = singleSeries.name || singleSeries.yKey;
        if (!name) {
            continue;
        }

        dataByName[name] = singleSeries.inDataSpace || [];
    }

    return dataByName;
}

/**
 * Figures out the min and max x values of the dependency data
 *
 * @param {Set<String>} dependencies
 * @param {Object} dataByName
 * @return {{minX: number, maxX: number}}
 */
function analyzeDependencyBounds({ dependencies, dataByName }) {
    let minX = Infinity;
    let maxX = -Infinity;

    for (let dependency of dependencies) {
        const data = dataByName[dependency];

        if (!data) {
            throw new Error(`Series ${dependency} does not exist`);
        }

        if (!data.length) {
            continue;
        }

        if (data[0][0] < minX) {
            minX = data[0][0];
        }

        if (data[data.length - 1][0] > maxX) {
            maxX = data[data.length - 1][0];
        }
    }

    return { minX, maxX };
}

/**
 * Evaluates an AST node recursively
 *
 * @param {*} node
 * @param {Object} dataByName
 * @param {Object} currentIndices
 * @param {Number} currentX
 * @param {Number} [depth]
 * @return {Number|Promise}
 */
function evaluateAST(node, { dataByName, currentIndices, currentX, depth=0 }) {
    const { type } = node;

    if (type === 'constant') {
        return node.value;
    }

    if (type === 'identifier') {
        const { name, modifier } = node;
        const data = dataByName[name];
        const index = currentIndices[name];
        if (typeof index !== 'number' || index >= data.length) {
            return null;
        }

        const [x, y] = data[index];

        if (modifier) {
            const { selector } = modifier;

            if (selector === 'match' && x !== currentX) {
                return null;
            }

            if (selector === 'prev') {
                if (index === 0) {
                    return null;
                } else {
                    return data[index - 1][1];
                }
            }

            if (selector === 'next') {
                if (index >= data.length - 1) {
                    return null;
                } else {
                    return data[index + 1][1];
                }
            }

            if (selector === 'last' && modifier.arguments.length) {
                if (modifier.arguments.length !== 1) {
                    throw new Error('Modifier :last may have at most one arguments');
                }

                const count = modifier.arguments[0];
                if (count === 'all') {
                    return data.slice(0, index + 1);
                } else {
                    if (!isPositiveInteger(count)) {
                        throw new Error('Modifier :last(n) may only be called with all or a positive integer');
                    }
                    return data.slice(Math.max(index - count + 1, 0), index + 1);
                }
            }

            if (selector === 'range') {
                if (modifier.arguments.length !== 2) {
                    throw new Error('Modifier :range must have two arguments');
                }

                let [beforeCount, afterCount] = modifier.arguments;

                if (beforeCount === 'all' && afterCount === 'all') { // helpful for memoization of functions
                    return data;
                }

                if (beforeCount === 'all') {
                    beforeCount = index;
                }

                if (afterCount === 'all') {
                    afterCount = data.length - index - 1;
                }

                if (!isPositiveInteger(beforeCount) || !isPositiveInteger(afterCount)) {
                    throw new Error('Modifier :range(n, m) may only be called with all or a positive integers');
                }

                return data.slice(Math.max(index - beforeCount, 0), index + afterCount + 1);
            }
        }

        return y;
    }

    if (type === 'function') {
        const args = [];
        let isAsync = false;

        for (let argNode of node.arguments) {
            const arg = evaluateAST(argNode, { dataByName, currentIndices, currentX, depth: depth + 1 });
            if (arg === null || arg === undefined) {
                return null;
            }

            args.push(arg);

            isAsync = isAsync || arg instanceof Promise;
        }

        const func = lookUpFunction(node.name);
        const parameters = { currentIndices, currentX, dataByName, node };

        if (isAsync) {
            return Promise.all(args).then(() => func(args, parameters));
        }

        return func(args, parameters);
    }

    throw new Error(`Invalid type in AST: ${type}`);
}

function isPositiveInteger(value) {
    return typeof value === 'number' && value >= 0 && Math.round(value) === value && !isNaN(value) && isFinite(value);
}
