/**
 * Parses the formula into tokens
 *
 * @param {String} formula
 * @return {[String]}
 */
export default function parseTokens(formula) {
    const tokens = [];
    let tokenStart = 0;
    let quoteOpen = false;
    let escapes = [];

    const pushToken = (end) => {
        if (tokenStart !== end) {
            let token = formula.slice(tokenStart, end);

            for (let i = escapes.length-1; i >= 0; i--) {
                const deletionIndex = escapes[i] + 1;
                token = token.slice(0, deletionIndex - 1) + token.slice(deletionIndex);
            }

            tokens.push(token);
            escapes = [];
        }
        tokenStart = end + 1;
    };

    for (let i = 0; i < formula.length; i++) {
        const character = formula[i];

        if (
            !quoteOpen && (
                // an operator
                (character === '(') ||
                (character === ')') ||
                (character === '+') ||
                (character === '-') ||
                (character === '*') ||
                (character === '/') ||
                (character === '%') ||
                (character === ',') ||
                (character === ':')
            )
        ) {
            pushToken(i);
            tokens.push(character);
        }

        if (character === ' ' || character === '\n') {
            pushToken(i);
        }

        if (character === '\\') { // skip the next character
            escapes.push(i - tokenStart);
            i++;
        }

        if (character === '"') {
            if (quoteOpen) {
                quoteOpen = false;
                pushToken(i);
            } else {
                if (i !== tokenStart) {
                    throw new Error('Unexpected quote partway through an identifier -- did you mean to escape it?');
                }

                quoteOpen = true;
                tokenStart++;
            }
        }
    }

    pushToken(formula.length);

    return tokens;
}
