/**
 * Figures out the order in which to calculate the series to minimize dependency conflicts
 * Requires ASTs to have been parsed beforehand
 *
 * @param series
 * @param [changedSeries]
 * @return {[]}
 */
import inferType from '../infer_type';

/**
 * Figures out the order in which to evaluate the changed series
 * If changedSeries is present and different from series, it will assume every series NOT in changedSeries
 * has already been calculated and is safe to ignore in the dependency chain
 *
 * @param {[Object]} series             - all series available
 * @param {[Object]} [changedSeries]    - the series that changed. Defaults to all series
 * @return {[]}
 */
export default function getCalculationOrder({ series, changedSeries }) {
    const addedSeriesNames = new Set([]);

    if (changedSeries) {
        const changedSeriesSet = new Set(changedSeries);

        for (let singleSeries of series) {
            if (!changedSeriesSet.has(singleSeries)) {
                if (singleSeries.name || singleSeries.yKey) {
                    addedSeriesNames.add(singleSeries.name || singleSeries.yKey);
                }
            }
        }
    } else {
        changedSeries = series;
    }

    const order = [];
    const remainingSeries = new Set(changedSeries);
    let prevLoopSize = null;

    while (remainingSeries.size > 0 && remainingSeries.size !== prevLoopSize) {
        prevLoopSize = remainingSeries.size;

        for (let singleSeries of remainingSeries) {
            if (inferType(singleSeries) !== 'formula') {
                order.push(singleSeries);
                remainingSeries.delete(singleSeries);

                if (singleSeries.name || singleSeries.yKey) {
                    addedSeriesNames.add(singleSeries.name || singleSeries.yKey);
                }

                continue;
            }

            if (!singleSeries.ast) { // ast parsing failed; pass it along to evaluate to nothing later
                order.push(singleSeries);
                remainingSeries.delete(singleSeries);

                if (singleSeries.name) {
                    addedSeriesNames.add(singleSeries.name);
                }

                continue;
            }

            if (singleSeries.ast.formula !== singleSeries.data.formula) {
                throw new Error('AST must be parsed');
            }

            let hasAllDependencies = true;
            for (let dependency of singleSeries.ast.dependencies) {
                if (!addedSeriesNames.has(dependency)) {
                    hasAllDependencies = false;
                    break;
                }
            }

            if (hasAllDependencies) {
                order.push(singleSeries);
                remainingSeries.delete(singleSeries);

                if (singleSeries.name) {
                    addedSeriesNames.add(singleSeries.name);
                }
            }
        }
    }

    // add all remaining, despite them not having dependencies
    if (remainingSeries.size > 0) {
        for (let singleSeries of remainingSeries) {
            order.push(singleSeries);
        }
    }

    return order;
}
