import {OPERATOR_FUNCTIONS} from './function_library/operators';
import {FILTER_FUNCTIONS} from './function_library/filters.js';
import {getPythonFunctionWrapper} from './function_library/python.js';

const FUNCTION_TABLE = {
    ...OPERATOR_FUNCTIONS,
    ...FILTER_FUNCTIONS,
    sum: ([tuples]) => {
        let sum = 0;
        for (let tuple of tuples) {
            sum += tuple[1];
        }
        return sum;
    },
    mean: ([tuples]) => {
        let sum = 0;
        for (let tuple of tuples) {
            sum += tuple[1];
        }
        return sum / tuples.length;
    }
};

export default function lookUpFunction(name) {
    let func = FUNCTION_TABLE[name];

    if (!func && name in Math) {
        func = Math[name];
    }

    if (!func && typeof window[name] === 'function') {
        func = window[name];
    }

    if (!func && name.startsWith('scipy.')) {
        func = getPythonFunctionWrapper(name);
    }

    if (!func) {
        throw new Error(`Unknown function ${name}`);
    }

    return func;
}
