/**
 * Converts a function that returns a
 *
 * @param {Function} func
 * @param {Boolean} memoize
 * @return {function(*=, {currentIndices?: *}): *}
 */
import memoizeFunction from './memoize_function.js';

export default function rangeFunction(func, {memoize=true}={}) {
    if (memoize) {
        return rangeFunction(memoizeFunction(func), { memoize: false });
    }

    return (args, { currentIndices }) => {
        const range = func(args);
        if (Object.values(currentIndices).length !== 1) {
            throw new Error('Function can only accept one variable as an argument');
        }

        return range[Object.values(currentIndices)[0]];
    };
}
