export default function enablePyodide() {
    if (window.pyodide) {
        return;
    }

    const src = 'https://cdn.jsdelivr.net/pyodide/v0.16.1/full/pyodide.js';

    if (document.querySelector(`script[src="${src}"]`)) {
        return;
    }

    const scriptEl = document.createElement('script');
    scriptEl.src = src;
    document.getElementsByTagName('head')[0].appendChild(scriptEl);
}
