export function calculatePrecision(value) {
    return Math.max(-Math.log10(Math.abs(value)) + 2, 0);
}

export function calculateTimePrecision(minDate, maxDate) {
    minDate = new Date(minDate);
    maxDate = new Date(maxDate);

    const range = maxDate.valueOf() - minDate.valueOf();
    if (range < 3*1000) {
        return 'ms';
    } else if (range < 10*60*1000) {
        return 's';
    } else {
        return 'm';
    }
}

export function roundToPrecision(value, precision=null) {
    if (precision === null) {
        precision = calculatePrecision(value);
    }

    if (isNaN(precision) || precision > 100) {
        return value.toString();
    }

    const rounded = value.toFixed(Math.ceil(precision));
    let stripped = rounded;
    if (rounded.includes('.')) {
        stripped = stripped.replace(/\.?0+$/g, '');
    }

    if (stripped === '') {
        return '0';
    }

    return stripped;
}

function formatTime(time, {precision, justTime, justDate}) {
    let timeString = `${time.getHours().toString().padStart(2, '0')}:${time.getMinutes().toString().padStart(2, '0')}`;

    if (precision === 's' || precision === 'ms') {
        timeString += `:${time.getSeconds().toString().padStart(2, '0')}`;
    }

    if (precision === 'ms') {
        timeString += `.${time.getMilliseconds().toString().padStart(3, '0')}`;
    }

    if (justDate) {
        return time.toLocaleDateString();
    }

    if (justTime) {
        return timeString;
    }

    return time.toLocaleDateString() + ' ' + timeString;
}

export function formatX(x, {dates=false, precision=null, justTime=false, justDate=false}={}) {
    if (dates && !(x instanceof Date)) {
        x = new Date(x);
    }

    if (x instanceof Date) {
        return formatTime(x, {precision, justTime, justDate});
    }

    if (isNaN(x)) {
        return 'NaN';
    }

    return roundToPrecision(x, precision);
}

export function formatY(y, {precision=null, log=false}={}) {
    if (y === null) {
        return 'null';
    }

    if (isNaN(y)) {
        return 'NaN';
    }

    if (log) {
        return `10^${roundToPrecision(Math.log10(y), precision)}`;
    }

    return roundToPrecision(y, precision);
}
