/**
 * Memoizes a function with a buffer size of one, returning the value of the previous run if the arguments matched
 * This is especially useful with formulas that operate on an entire range; this can be used to call them once per
 * series rather than once per point
 *
 * @param {Function} func
 * @return {function(*=, *=): (*)}
 */
export default function memoizeFunction(func) {
    memoizeFunction.memoized = memoizeFunction.memoized || {};
    memoizeFunction.memoized[func] = memoizeFunction.memoized[func] || {};

    return (args, params) => {
        const lastRun = memoizeFunction.memoized[func];

        if (argumentsMatch(lastRun.args, args, lastRun.argLengths)) {
            return lastRun.result;
        }

        const result = func(args, params);
        memoizeFunction.memoized[func] = {
            args,
            argLengths: args.map((arg) => Array.isArray(arg) ? arg.length : null),
            result
        };
        return result;
    };
}

/**
 * Checks if arguments match
 *
 * @param {Array} prevArgs  - the previous args
 * @param {Array} args      - the new args
 * @param {Array<Number|null>} prevArgLengths - the lengths of any previous array args (or null if arg not array)
 * @return {boolean}
 */
function argumentsMatch(prevArgs, args, prevArgLengths) {
    if (!prevArgs || !args) {
        return false;
    }

    if (prevArgs.length !== args.length) {
        return false;
    }

    for (let i = 0; i < args.length; i++) {
        if (prevArgs[i] !== args[i]) {
            return false;
        }

        if (prevArgLengths[i] !== null && prevArgLengths[i] !== args[i].length) {
            return false;
        }
    }

    return true;
}
