/**
 * Converts from data space to selected space
 *
 * @param {Array<Array<Number>>} data
 * @param {Array} [swap]
 * @param {Number} minX
 * @param {Number} maxX
 * @param {Boolean} ignoreDiscontinuities
 * @param {Boolean} square
 * @return {Array<Array<Number>>}
 */
import binarySearch from '../../helpers/binary_search';

export default function dataSpaceToSelectedSpace({ data, swap, minX, maxX, ignoreDiscontinuities, square }) {
    let firstIndex = binarySearch(data, minX, { searchType: 'before', returnIndex: true }) || 0;
    let lastIndex = binarySearch(data, maxX, { searchType: 'after', returnIndex: true }) || 0;

    let inSelectedSpace;

    if (swap) {
        inSelectedSpace = swap;
        for (let i = 0; i < lastIndex - firstIndex - 1; i++) {
            inSelectedSpace[i] = data[i + firstIndex + 1];
        }
        inSelectedSpace.splice(lastIndex - firstIndex - 1);
    } else {
        inSelectedSpace = data.slice(firstIndex + 1, lastIndex);
    }

    if (firstIndex !== -1 && minX < data[firstIndex][0]) {
        inSelectedSpace.unshift(data[firstIndex]);
    }

    if (lastIndex !== -1 && maxX > data[lastIndex][0]) {
        inSelectedSpace.push(data[lastIndex]);

        if (square) {
            inSelectedSpace.push([maxX, data[lastIndex][1]]);
        }
    }

    if (!inSelectedSpace.length || undateify(inSelectedSpace[0][0]) !== undateify(minX)) {
        inSelectedSpace.unshift([minX, interpolateFirst(data, firstIndex, minX)]);
    }

    if (!square && !inSelectedSpace.length || undateify(inSelectedSpace[inSelectedSpace.length - 1][0]) !== undateify(maxX)) {
        inSelectedSpace.push([maxX, interpolateLast(data, lastIndex - 1, maxX)]);
    }

    if (ignoreDiscontinuities) {
        return inSelectedSpace.filter(([_x, y], i) => (y !== null && y !== undefined) || i === 0 || i === inSelectedSpace.length - 1);
    }

    return inSelectedSpace;
}

function undateify(potentialDate) {
    if (potentialDate instanceof Date) {
        return potentialDate.valueOf();
    }

    return potentialDate;
}

/**
 * Finds the point at the boundary via interpolation
 *
 * @param {Array<Array<Number>>} data
 * @param {Number} firstIndex
 * @param {Number} minX
 * @return {null|*}
 */
function interpolateFirst(data, firstIndex, minX) {
    if (firstIndex < 0) {
        return null;
    }

    if (firstIndex + 1 >= data.length) {
        return null;
    }

    const [xBefore, yBefore] = data[firstIndex];
    const [xAfter, yAfter] = data[firstIndex + 1];

    if (minX === xBefore && yBefore !== null) {
        return yBefore;
    }

    if (minX === xAfter && yAfter !== null) {
        return yAfter;
    }

    if (yBefore === null || yAfter === null) {
        return null;
    }

    const percent = (minX - xBefore)/(xAfter - xBefore);
    if (percent < 0) {
        return null;
    }

    return percent*(yAfter - yBefore) + yBefore;
}

/**
 * Finds the point at the boundary via interpolation
 *
 * @param {Array<Array<Number>>} data
 * @param {Number} lastIndex
 * @param {Number} maxX
 * @return {null|*}
 */
function interpolateLast(data, lastIndex, maxX) {
    if (lastIndex >= data.length - 1) {
        return null;
    }

    if (lastIndex < 0) {
        return null;
    }

    const [xBefore, yBefore] = data[lastIndex];
    const [xAfter, yAfter] = data[lastIndex + 1];

    if (maxX === xBefore && yBefore !== null) {
        return yBefore;
    }

    if (maxX === xAfter && yAfter !== null) {
        return yAfter;
    }

    if (yBefore === null || yAfter === null) {
        return null;
    }

    const percent = (maxX - xBefore)/(xAfter - xBefore);
    if (percent > 1) {
        return null;
    }

    return percent*(yAfter - yBefore) + yBefore;
}
